.back-call {
  @include vp-1023 {
    display: none;
  }
}

.back-call__button {
  min-width: 160px;
  min-height: 40px;
  font-size: 14px;
  letter-spacing: 1px;
  text-align: center;
  text-transform: uppercase;
  color: $color-default-white;
  background-color: transparent;
  border: 1px solid $color-default-white;
  border-radius: 3px;
  cursor: pointer;
}
